.main-container{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}
.search-bar input{
    overflow: hidden;
    margin: 10px;
}
.name{
    color:red;
    font-weight:100%;
    font-size: 20px;
}
.text-search{
    border-radius: 10px;
    width: 250px;
    height: 30px;
    text-align: center;
    padding: 10px;
    background: lightblue;
}
input:focus{
    outline: none
}
button{
    width: 200px;
    height: 30px;
    margin: 10px;
    border: 1px solid;
    border-radius: 10px;
    cursor: pointer;
    background: lightblue;
}
button:hover{
    background-color:white;
}
button:focus{
    outline: none;
}
.button-group{
    display: flex;
    justify-content: center;
}
@keyframes animatedBackground {
    from {
      background-position: 0 0;
    }
    to {
      background-position: 100% 0;
    }
  }
  #animate-area {
    justify-content: center;
    width: 200px;
    height: 200px;
    background-image: url(http://placekitten.com/400/200);
    background-position: 0px 0px;
    background-repeat: repeat-x;
    animation: animatedBackground 10s linear infinite alternate;
  }
  textarea{
      width: 400px;
      height: 500px;
      background-color: lightblue;
  }
  .displays{
      padding-top: 50px;
  }